import React from "react";
import Prism from "prismjs";

export class PrismCode extends React.Component {
  constructor(props) {
    super(props);
    this.ref = React.createRef();
  }
  componentDidMount() {
    this.highlight();
  }
  componentDidUpdate() {
    this.highlight();
  }
  useEffect() {
    Prism.highlightAll();
    };
  highlight = () => {
    if (this.ref && this.ref.current) {
      Prism.highlightElement(this.ref.current);
    }
  };
  render() {
    const { code, plugins, language } = this.props;
    return (
      <pre class={!plugins ? "" : plugins.join(" ")}>
        <code ref={this.ref} class={`language-${language}`}>
          {code.trim()}
        </code>
      </pre>
    );
  }
}

