import React, { Component, useEffect } from "react";
import {PrismCode} from "../components/prismCode"
import Prism from "prismjs"
import 'prismjs/components/prism-python'
import 'prismjs/plugins/copy-to-clipboard/prism-copy-to-clipboard';
import 'prismjs/plugins/toolbar/prism-toolbar';
import "prismjs/plugins/line-numbers/prism-line-numbers";
import "prismjs/plugins/line-numbers/prism-line-numbers.css"

const code = `
from requests import get
from requests.exceptions import RequestException
from contextlib import closing
from bs4 import BeautifulSoup
`.trim()

const code2 = `
def getJobs(self):
regularJobs = self.driver.find_element_by_xpath('//*[@id="wd-FacetValue-CheckBox-workerSubType::41059f5be019104091d88ac710bc4319"]/div')
postDocJobs = self.driver.find_element_by_xpath('//*[@id="wd-FacetValue-CheckBox-workerSubType::41059f5be019104091d88f33e9ce431b"]/div')
regularJobs.click()
postDocJobs.click()
time.sleep(2)
soup = BeautifulSoup(self.driver.page_source, 'html.parser')
jobList = soup.findAll("span", class_="gwt-InlineLabel WK3F WJ2F")

jobIDs = list()
for job in range(len(jobList)):
	jobText = jobList[job].text
	jobIDs.append(jobText[:5])

self.driver.back()
return jobIDs
`.trim()


export default class Project1Content extends Component {
  render() {
    return (
      <div id="main" class="div-1">
        <section class="post">
          <header class="major">
            <h1>
              NREL Job Search
              <br />
              2020
            </h1>
            <p>
              Creating a webscraper to see what kind of degrees NREL is looking
              for
              <br />
              FYI if you're not interested in the code and process, scroll down
              until you see pretty figures
            </p>
          </header>

<section>
          <p>
            For the past few years, the National Renewable Energy Laboratories
            (NREL) has stood out as a potential target for after finishing my
            schooling. After working at Sandia National Labs in undergrad, I
            enjoyed the style of work done in the National Lab setting. It
            wasn't exactly like Industry and it wasn't exactly like academia
            either. Instead it seemed like the best of both. So in the interest
            of crafting my skills towards becoming an exemplary candidate, I
            decided to do some investigation of what degrees are most requested
            on NREL's job application webpage. I'm going to start with the
            project results, but there is a semi-walkthrough with embedded code
            after the presented figures
          </p>

          <a href="images/degreeFreq.png">
            <img src="images/degreeFreq.png" class='rightTab' alt="" />
          </a>

          </section>

          <p>
            I found it interesting that, in a research lab full of scientist and
            engineers, the most requested degree type was Computer Science It
            makes sense since we live in a time period that some coin the "Age
            of Data".
            <em>
              I mean, I'm making a data science portfolio for a reason I guess.
            </em>
            Following Computer Science are the degrees I expected to be most
            common. Physics, MechE, and Materials are the type of skill sets I
            was used to seeing at Sandia. Luckily my degree came in at a
            respectable 4<sup>th</sup> place. I was surprised however, that
            Chemistry fell into the bottom half, I did originally hypothesis
            that "Physical" was the 1<sup>st</sup> half of "Physical Chemisty"
            but that wouldn't matter because they still both occurred 6 times
            each, which means that they still only occured 6 times if they were
            originally paired together in job lisitngs
          </p>

          <p>
            Next was to satisfy the age old question: How many decades
            experience do I need? I love seeing Entry-level position postings
            that are like "14 years experience in X" expecially when the
            technology they want experience in has only existed for liek 6
            years... just saying.
          </p>

          <a href="images/workExp.png"width="100%">
            <img src="images/workExp.png" class='leftTab' alt="" />
          </a>

          <p>
            This figure made me a little more confident that doing a PhD was a
            good investement. On average only 3 years experience are required
            for the many postings that required a PhD. There wasn't too much of
            a difference, on averaage, between a Master's and a Bachelor's
            degree, but I will note that during data scraping I saw a lot of
            double digit numbers for the bachelors, but they were offset by a
            few positions that required lower amounts such as 5 years. The
            listings for a Master's for sure never exceeded 7 or 8 and
            absolutely never reached as high as 13 years experience like a
            Bachelor's degree
          </p>

          <p>
            These two figures answered my simple question:"what kind of degrees
            NREL is looking for?" and also showed be how much experience they
            want. For fun I threw in a couple Word Clouds that I shaped like the
            NREL logo The default is random colorized words on a white
            background, but the typography thickness doesn't do the contrast any
            favors so i also threw in a black and white variation which I think
            looks a little cleaner. The word cloud was composed of the "action
            words" that were present in the "Additional" and "Preffered"
            requirements. Some popular words that NREL seems to feel like
            represents a good candidate are: Strong, Analysis, Technical,
            Research, ability, Problem Solving, Communication Skills,
            Experience, Team, etc..
          </p>

          <div
              class="image bottom .col-lg-12 col-md-12 col-sm-12 col-xs-12 animate-box fadeInLeft animated" >
                <img src="images/wordCloudNREL_BW.png"
                 alt="Git" class="centerFull"></img>
            </div>
          {/* <a href="images/wordCloudNREL_BW.png" class="image fit">
            <img src="images/wordCloudNREL_BW.png" alt="" />
          </a> */}

          <PrismCode
            code={code2}
            language="python"
            plugins={["line-numbers"]}
        />

        
        </section>
        
      </div>
      
    );
  }
  useEffect() {
    Prism.highlightAll();
    };
}



